export class User {

    public id: number;
    public name: string;
    public email: string;
    public phone: string;
    public password: string;
    public enterprise: string;
    public type: number;
    public active: boolean;
    public created_at: string;
    public update_at: string;

    
    constructor(){
        this.enterprise = '';
        this.phone = '';
    }
}





